import loading from '../../components/loadding'
import payBall from '../../components/payBall'
import { mapState } from 'vuex' 
export default {
    components: {
        loading,
        payBall
    },
    onShareAppMessage: function( options ){
        return this.$commenShare()
    },
    data() {
        return {
            ticketData: null,
            venueIntro: '暂无场馆',
            venueNum: '0',
            strings: '', // 富文本内容
            sellType: null
        }
    },
    computed: {
        ...mapState(['locationObj','ticketBaseInfo','userInfo'])
    },
    onShow() {
        this.initTicketData()
    },
    methods: {
        async initTicketData() {
            if (!this.ticketBaseInfo || !this.ticketBaseInfo.id) {
                this.$tip.alertDialog(
                '请返回首页选择相关联票').then(() => {
                    uni.switchTab({
                        url: '/pages/main/main'
                    })
                })
                return
            }
            const res = await this.$api.getTicketDetail(this.ticketBaseInfo.id)
            if (res.code === '0') {
                this.ticketData = res.data
                this.sellType = JSON.parse(res.data.sellType)
                this.strings = res.data.description
                this.$store.commit('SET_TICKET_OBJ',this.ticketData)
                this.processVenueIntro()
            }
        },
        gotoPay () {
            let hasLogin = !!this.userInfo
            if (!hasLogin) {    
                this.$tip.toast('请先登录','none')          
                uni.navigateTo({
                    url: '../login/login',
                });
                return
            }
            uni.navigateTo({
                url: '../payPage/payPage'
            })
        },
        processVenueIntro () {
            let temp = this.ticketData.relatedVenueList
            if (temp.length === 0) {
                this.venueIntro = '暂无场馆'
            } else {
                let tempArr = []
                temp.forEach(item => {
                    console.log(item)
                    let obj = JSON.parse(item.relatedRedundancy)
                    tempArr.push(obj.text)
                });
                this.venueNum = tempArr.length
                this.venueIntro = tempArr.splice(0,3).join('，')
            }
        }
    },
}
